import React, { Component } from "react"

import FilterFooter from "../../../../components/FilterFooter"

import styles from "./index.module.css"

export default class FilterMore extends Component {
  state = {
    // 
    selected: this.props.value,
  }
  // 
  handlerSer = (value) => {
    // 
    const { selected } = this.state
    let newSelected = [...selected]
    // 
    let index = newSelected.indexOf(value)
    if (index < 0) {
      // 
      newSelected.push(value)
    } else {
      //  
      newSelected.splice(index, 1)
    }
    // state
    this.setState({
      selected: newSelected,
    })
  }

  // 
  renderFilters(data) {
    return data.map((item) => {
      return (
        <span
          onClick={() => {
            this.handlerSer(item.value)
          }}
          key={item.value}
          className={[
            styles.tag,
            this.state.selected.includes(item.value) ? styles.tagActive : "",
          ].join(" ")}
        >
          {item.label}
        </span>
      )
    })

    //  styles.tagActive
  }

  render() {
    const {
      onOk,
      onCancle,
      data: { characteristic, floor, oriented, roomType },
    } = this.props
    return (
      <div className={styles.root}>
        {/*  */}
        <div onClick={this.props.onCancle} className={styles.mask} />

        {/*  */}
        <div className={styles.tags}>
          <dl className={styles.dl}>
            <dt className={styles.dt}></dt>
            <dd className={styles.dd}>{this.renderFilters(roomType)}</dd>

            <dt className={styles.dt}></dt>
            <dd className={styles.dd}>{this.renderFilters(oriented)}</dd>

            <dt className={styles.dt}></dt>
            <dd className={styles.dd}>{this.renderFilters(floor)}</dd>

            <dt className={styles.dt}></dt>
            <dd className={styles.dd}>{this.renderFilters(characteristic)}</dd>
          </dl>
        </div>

        {/*  */}
        <FilterFooter
          onOk={() => {
            onOk(this.state.selected)
          }}
          onCancle={onCancle}
          className={styles.footer}
        />
      </div>
    )
  }
}
