import api from "../../axios.js"

// 

export function getFilter(id) {
  return api.get("/houses/condition", {
    params: {
      id,
    },
  })
}

// 
export function getHouses(cityId, filters, start, end) {
  return api.get("/houses", {
    params: {
      cityId,
      ...filters,
      start,
      end,
    },
  })
}
