/* eslint-disable no-undef */
import React, { Component } from "react"
import { Carousel, Flex, Grid, WingBlank, SearchBar } from "antd-mobile" //
import { BASE_URL } from "../../utils/axios.js" //axios
import { getSwiper, getGrid, getNews } from "../../utils/api/Home/index.js" // home
import Navs from "../../utils/navConfig.js" // 
import "./index.scss" // 
import { getCurrcity } from "../../utils/index.js"
// import { getCityInfo } from "../../utils/api/City/index.js"

class Index extends Component {
  state = {
    // 
    data: [],
    // 
    autoplay: false,
    // 
    imgHeight: 176,
    //  
    groups: [],
    // 
    news: [],
    // 
    keyword: "",
    // 
    curCity: {
      label: "--",
      value: "",
    },
  }

  // react
  componentDidMount() {
    this.getSwiper()
    this.getGroups()
    this.getNew()
    // this.loadDate()
    this.getCurcity()
  }

  // 
  getCurcity = async () => {
    const res = await getCurrcity()
    this.setState({
      curCity: {
        label: res.label,
        value: res.value,
      },
    })
  }

  // 
  renderTopNav = () => {
    return (
      <Flex justify="around" className="topNav">
        <div className="searchBox">
          <div
            className="city"
            onClick={() => {
              this.props.history.push("/cityList")
            }}
          >
            {this.state.curCity.label}
            <i className="iconfont icon-arrow" />
          </div>
          <SearchBar
            value={this.state.keyword}
            onChange={(v) => this.setState({ keyword: v })}
            placeholder=""
          />
        </div>
        <div
          className="map"
          onClick={() => {
            this.props.history.push("/map")
          }}
        >
          <i key="0" className="iconfont icon-map" />
        </div>
      </Flex>
    )
  }

  // 
  getSwiper = async () => {
    const res = await getSwiper()
    // console.log(res)
    if (res.status === 200) {
      this.setState(
        {
          data: res.body,
        },
        () => {
          this.setState({
            autoplay: true,
          })
        }
      )
    }
  }

  // 
  renderSwiper = () => {
    return (
      <Carousel autoplay={this.state.autoplay} infinite autoplayInterval={2000}>
        {this.state.data.map((val) => (
          <a
            key={val}
            href="http://www.itheima.com/"
            style={{
              display: "inline-block",
              width: "100%",
              height: this.state.imgHeight,
            }}
          >
            <img
              src={`${BASE_URL}${val.imgSrc}`}
              alt=""
              style={{
                width: "100%",
                verticalAlign: "top",
              }}
              onLoad={() => {
                // fire window resize event to change height
                window.dispatchEvent(new Event("resize"))
                this.setState({
                  imgHeight: "auto",
                })
              }}
            />
          </a>
        ))}
      </Carousel>
    )
  }

  // 
  NavigationBar = () => {
    return (
      <Flex className="nav">
        {Navs.map((item) => {
          return (
            <Flex.Item
              key={item.id}
              onClick={() => {
                this.props.history.push(item.path)
              }}
            >
              <img alt="" src={item.img} />
              <p>{item.title}</p>
            </Flex.Item>
          )
        })}
      </Flex>
    )
  }

  //  
  getGroups = async () => {
    const res = await getGrid()
    if (res.status === 200) {
      this.setState({
        groups: res.body,
      })
    }
  }
  //  
  getGrids = () => {
    return (
      <div className="group">
        <Flex className="group-title" justify="between">
          <h3></h3>
          <span></span>
        </Flex>

        <Grid
          square={false}
          hasLine={false}
          data={this.state.groups}
          columnNum={2}
          renderItem={(item) => (
            // item
            <Flex className="grid-item" justify="between">
              <div className="desc">
                <h3>{item.title}</h3>
                <p>{item.desc}</p>
              </div>
              <img src={`${BASE_URL}${item.imgSrc}`} alt="" />
            </Flex>
          )}
        />
      </div>
    )
  }

  // 
  getNew = async () => {
    const res = await getNews()
    if (res.status === 200) {
      this.setState({
        news: res.body,
      })
    }
  }

  // 
  renderNews() {
    return this.state.news.map((item) => (
      <div className="news-item" key={item.id}>
        <div className="imgwrap">
          <img className="img" src={`${BASE_URL}${item.imgSrc}`} alt="" />
        </div>
        <Flex className="content" direction="column" justify="between">
          <h3 className="title">{item.title}</h3>
          <Flex className="info" justify="between">
            <span>{item.from}</span>
            <span>{item.date}</span>
          </Flex>
        </Flex>
      </div>
    ))
  }

  // promise

  // loadDate = async () => {
  //   // let res = await Promise.all([1, 2, 3, 4])
  //   let res = await Promise.all([getSwiper(), getGroups(), getNew()])
  //   console.log(res)
  // }
  render() {
    return (
      <div className="index">
        {
          //  
          this.renderSwiper()
        }
        {
          // 
          this.NavigationBar()
        }
        {
          // 
          this.getGrids()
        }
        {
          // 
          <div className="news">
            <h3 className="group-title"></h3>
            <WingBlank size="md">{this.renderNews()}</WingBlank>
          </div>
        }
        {
          // 
          this.renderTopNav()
        }
      </div>
    )
  }
}

export default Index
